
.sidenav{
	height: 100%;
	width: 200px;
	position: fixed;
	z-index: 6;
	top: 0;
	left: -200px;
	background-color: white;
	overflow-x: hidden;	
	padding-top: 60px;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}
.sidenav a{
	padding: 8px 8px 8px 32px;
	text-decoration: none;
	font-size: 25px;
	color: #818181;
	display: block;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
}
.sidenav a:hover{
	color: #c9c9c9;
}
.sidenav .closebtn{
	position: absolute;
	top: 0;
	right: 25px;
	font-size: 36px;
	margin-left: 50px;
}


